:host {
    flex: 1;
}

.video-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, 400px);
    grid-gap: 1rem;
}

.technique-card {
    border: 1px solid gray;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    border-radius: 0.5rem;
    -webkit-box-shadow: 0px 7px 16px -1px rgba(0,0,0,0.34); 
    box-shadow: 0px 7px 16px -1px rgba(0,0,0,0.34);
}

.summary-container {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}

img {
    width: 100%;
}

.technique-card button {
    width: 100%;
    padding-top: 0.35rem;
    padding-bottom: 0.35rem;
    line-height: 2rem;
    background-color: rgb(56, 55, 55);
    color: white;
    border: none;
    font-size: 1rem;
}

.technique-card button:hover {
    cursor: pointer;
}

@media (max-width: 800px) {
    .video-container {
        grid-template-columns: 1fr;
    }
  }