import { Component, Prop, Event, EventEmitter, h, State } from '@stencil/core';

@Component({
  tag: 'video-viewer',
  styleUrl: 'video-viewer.css',
  shadow: false,
})
export class VideoViewer {
    @Prop() video_id: string;
    @Prop() technique_name: string;

    @Event() closeVideoViewer: EventEmitter;
    @State() iframeLoaded: boolean = false;
    
    render() {
        return <div class="video-viewer-modal">
            <div class="video-content-container">
                <div class={this.iframeLoaded ? "close-player visible" : "close-player"} onClick={()=> this.handleCloseClick()}>X Close player</div>
                <div>
                    <iframe onLoad={() => this.iframeLoaded = true} src={`https://www.youtube.com/embed/${this.video_id}?rel=0`} frameborder="0" allowfullScreen></iframe>
                </div>
                <div class={this.iframeLoaded ? "technique-name visible" : "technique-name"}>{this.technique_name}</div>
            </div>
        </div>;
    }

    handleCloseClick() {
        this.closeVideoViewer.emit()
    }

}