import { Component, Prop, h, Event, EventEmitter } from '@stencil/core';

@Component({
  tag: 'techniques-component',
  styleUrl: 'techniques-component.css',
  shadow: false,
})
export class TechniquesComponent {
    @Prop() selectedCategory?: {category: string; subcategory: string};
    @Prop() techniques?: any[];

    @Event() showVideo: EventEmitter;
    
    render() {
        return <div>
        <h3>{this.selectedCategory ? `${this.selectedCategory.category} > ${this.selectedCategory.subcategory}`: ""}</h3>
        <hr></hr>
        <div class="video-container">
            {this.techniques?.map((technique) => {
                    return <div class="technique-card">
                        <div class="summary-container">
                            <div><img src={`https://img.youtube.com/vi/${technique.video_id}/mqdefault.jpg`} loading='lazy'></img></div>
                            <h4>{technique.technique_name}</h4>
                            <p>{technique.description}</p>
                        </div>
                        <button onClick={() => this.showVideo.emit({video_id: technique.video_id, technique_name: technique.technique_name})}>View technique</button>
                    </div>
                })
            }
        </div>
        </div>;
    }
}
