import { Component, h, Prop, Event, EventEmitter } from '@stencil/core';

@Component({
  tag: 'app-menu',
  styleUrl: 'app-menu.css',
  shadow: false,
})

export class AppMenu {
  @Prop() menuData: any[];
  @Prop() selectedCategory: {category: string; subcategory: string};

  @Event() menuItemSelected: EventEmitter<{ category: string}>;

  @Event() submenuItemSelected: EventEmitter<{ category: string; subcategory: string }>;

  selectCategory(categoryName: string) {
    this.menuItemSelected.emit({ category: categoryName});
  }

  selectSubcategory(subcategoryName: string) {
    this.submenuItemSelected.emit({ category: this.selectedCategory.category, subcategory: subcategoryName });
  }

  render() {
    return (
        <aside>
            <h3>Categories</h3>
            <hr></hr>
            <ul>
                {this.menuData.map((category) => (
                <li class="category">
                    <div class="category-header" onClick={() => this.selectCategory(category.name)}>
                    <h4>{category.name}</h4>
                    </div>
                    {this.selectedCategory?.category === category.name ? 
                    <ul class="subcategory-list">
                        {category.subcategories.map((subcategory) => (
                        <li onClick={() => this.selectSubcategory(subcategory.name)} class={subcategory.name === this.selectedCategory?.subcategory ? "subcategory-item selected" : "subcategory-item"}>
                            {subcategory.name}
                        </li>
                        ))}
                    </ul>
                    : null
                    }
                </li>
                ))}
            </ul>
        </aside>
    );
  }
}
