import { Component, State, h , Event, EventEmitter} from '@stencil/core';
import { auth } from '../../utils/auth';

@Component({
  tag: 'login-page',
  styleUrl: 'login-page.css',
  shadow: true,
})
export class LoginPage {

  @State() enteringPassword = "";
  @State() errorMessage = "";

  @Event() loggedIn: EventEmitter;

  render() {
   return <div class="login-page">
      <div class="login-panel">
          <h3>Enter password</h3>
          <input type="text" value={this.enteringPassword} onInput={this.handlePasswordChange.bind(this)} ></input>
          <button disabled={this.enteringPassword.length === 0} onClick={() => this.submitPassword()}>Submit password</button>
          <h3 class="error-message">{this.errorMessage}</h3>
      </div>
   </div>
  }

  handlePasswordChange(event: Event) {
    const el = event.target as HTMLInputElement;
    this.enteringPassword = el.value;
    this.errorMessage = "";
  }

  submitPassword() {
    if(this.enteringPassword === atob(auth)) {
      this.enteringPassword = "";
      this.loggedIn.emit();
    }
    else {
      this.enteringPassword = "";
      this.errorMessage = "Invalid password"
    }
  }
}
