.video-viewer-modal {
    position: fixed;
    z-index: 1000;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    background-color: rgb(0,0,0,0.8);
    display: flex;
    justify-content: center;
    align-items: center;
}

.video-content-container {
    position: relative;
    padding: 2rem;
}

.close-player {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1001;
    color: whitesmoke;
    visibility: hidden;
    padding: 0.25rem;
}

.close-player:hover {
    font-weight: bold;
    cursor: pointer;
}

.technique-name {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    color: whitesmoke;
    padding: 0.25rem;
    width: 100%;
    visibility: hidden;
}

.visible {
    visibility: visible;
}

iframe {
    width: 768px;
    height: 432px;
}

@media (max-width: 800px) {
    .video-content-container {
        padding-left: 0.25rem;
        padding-right: 0.25rem;
        padding-bottom: 0.5rem;
    }

    iframe {
        width: 100%;
        height: 250px;
    }
    
  }