.login-page {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(56, 55, 55);
    color: white;
}

.login-panel {
    width: 250px;
    padding: 1rem;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
}

h3 {
    margin-bottom: 0.5rem;;
    margin-top: 0.5rem;
}

input {
    flex:1;
    min-height: 40px;
    margin-bottom: 0.5rem;
    padding: 0.25rem;
    font-size: 1rem;
}

.error-message {
    color: orange;
}

button {
    flex:1;
    min-height: 50px;
    padding: 0.25rem;
    font-size: 1rem;
    background-color: rgb(56, 55, 55);;
    color: white;
    border: 1px solid white;
}

button:hover {
    cursor: pointer;
}