aside {
    padding-left: 0.5rem;
    padding-right: 1rem;
}

ul {
    list-style-type: none;
    padding-left: 0;
}

li {
    cursor: pointer;
}

.category-header {
    width: 100%;
    box-sizing: border-bo;
}

li.subcategory-item {
    min-height: 2rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
}

.subcategory-item.selected {
    background-color: rgb(56, 55, 55);
    color: white;
}