import { Component, Listen, State, h } from '@stencil/core';
import { categories } from '../../utils/techniques';

@Component({
  tag: 'app-main',
  styleUrl: 'app-main.css',
  shadow: false,
})
export class App {

  @State() selectedCategory: {category: string; subcategory: string} = {category: categories[0].name, subcategory: categories[0].subcategories[0].name};

  @State() videoViewer: {video_id: string; technique_name: string} | null = null;

  @State() authenticated: boolean = false;

  @Listen("loggedIn")
  loggedInEvent() {
    this.authenticated = true;
  }

  @Listen("menuItemSelected")
  handleMenuItemSelected(event: CustomEvent<{ category: string;}>) {

    const category = categories.find(cat => cat.name === event.detail.category);
    
    this.selectedCategory = {category: event.detail.category, subcategory: category?.subcategories[0].name};
  }
  
  @Listen("submenuItemSelected")
  handleSubmenuItemSelected(event: CustomEvent<{ category: string; subcategory: string }>) {
    this.selectedCategory = event.detail;
  }

  @Listen("closeVideoViewer")
  closeVideoViewer() {
    this.videoViewer = null;
  }

  @Listen("showVideo")
  showVideo(event: CustomEvent<{ video_id: string; technique_name: string }>) {
    this.videoViewer = event.detail;
  }

  getTechniques() {
    if(this.selectedCategory) {
      const displayedCategory = categories.find(cat => cat.name === this.selectedCategory.category);
      if(displayedCategory) {
        const displayedSubCategory = displayedCategory.subcategories.find(subcat => subcat.name === this.selectedCategory.subcategory);
        if(displayedSubCategory) {
          return displayedSubCategory.techniques;
        } 
      }
    }
    return []
  }

  render() {
    if(!this.authenticated) {
      return <login-page></login-page>
    }
    else {
      const techniques = this.getTechniques.bind(this)()
      return <div class="app-container">
        {
          this.videoViewer
          ? <video-viewer video_id={this.videoViewer.video_id} technique_name={this.videoViewer.technique_name}></video-viewer>
          : null
        }
        <header>
          Judo techniques
        </header>
        <main >
          <app-menu menuData={categories} selectedCategory={this.selectedCategory}></app-menu>
          <techniques-component techniques={techniques} selectedCategory={this.selectedCategory}></techniques-component>
        </main>
        <footer>
        </footer>
      </div>;
    }
  }
}
